.NavbarItems {
    background: linear-gradient(90deg, rgb(129, 115, 255) 0%, rgba(73,63,252,1) 100%); 
    height: 80px; 
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
}

li {
    cursor: pointer;
}

.navbar-logo {
    color: #fff; 
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
}

.logo-icon {
    margin-left: 10px; 
    font-size: 1.6rem;
}

.nav-menu {
    display: grid; 
    grid-template-columns: repeat(3, auto);
    grid-gap: 10px;
    list-style-type: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-links {
    color: white; 
    text-decoration: none;
    padding: 0.5rem 1rem; 
}

.nav-links:hover {
    background-color: #6d76f7;
    border-radius: 4px;
    transition: all .2s ease-out; 
}

.fa-bars {
    color: #fff; 
}

.nav-links-mobile {
    display: none; 
}

.menu-icon {
    display: none; 
}

@media screen and (max-width: 960px) {
    .NavbarItems {
        position: relative;
    }
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 80px;
        /*When left is made 0 it's easy to see what I'm trying to do.*/
        left: -100%;
        opacity: 1;
        transition: all 0.5s all ease;
    }

    .nav-menu.active {
        background: #6668f4;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }
    
    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }
    
    .nav-links:hover {
        background-color: #7577f8;
        border-radius: 0;
    }
    
    .navbar-logo {
        position: absolute;
        top: 0; 
        left: -40px; 
        transform: translate(25%, 50%);
    }
    
    .menu-icon {
        display: block;
        position: absolute;
        top: 0; 
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;

    }
    .fa-times {
        color: #fff;
        font-size: 2rem;
    }
    .nav-links-mobile {
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        background: #4ad9e4;
        text-decoration: none;
        color: white;
        font-size: 1.5rem;
    }
    .nav-links-mobile:hover {
        background: #fff;
        color: #6568f4;
        transform: 250ms; 
    }
    Button {
        display: none;
    }
}
